.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8rem;
}

.logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo-text {
    font-size: 4rem;
    font-weight: 300;
    color: var(--color-white);
}

.nav-links {
    display: flex;
}

.nav-links li {
    margin-left: 1rem;
    list-style: none;
}

.nav-links a {
    font-size: 1.6rem;
    font-weight: 300;
    color: var(--color-white);
    text-decoration: none;
    margin-right: 1rem;
}

.nav-links a:hover {
    color: orangered;
}

a.btn {

    border-radius: 5px;
    margin-left: 1rem;
    font-weight: 500;
    padding: 5px 8px;
}

a.btn-dark {
    border: 2px solid var(--color-white);
    background: transparent;
}

a.btn-orange {
    border: 2px transparent;
    background: orangered;
    color: var(--color-white);
}

.navbar .menu-icons {
    display: none;
}

@media screen and (max-width: 769px) {
    .nav-btn {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .nav-btn {
        display: block;
    }
    
    .nav-links {
        display: none;
        border: 2px solid var(--color-white);
        /*z-index: 111;*/
    }

    .navbar .menu-icons {
        display: block;
    }

    .navbar #nav-links-mobile, .navbar #nav-links-mobile-hide {
        display: block;
        position: absolute;
        left: 0;
        top: 8rem;
        width: 100%;
        background: var(--dark-blue);
        border-top: 1px solid var(--color-white);
        padding-bottom: 5px;
        transition: all 0.3s;
    }

    .navbar #nav-links-mobile{
        animation: slideIn 0.5s ease-in-out;
    }
    @keyframes slideIn {
        0% {
            transform: translateX(-100%);
        }
        100%{
            transform: translateX(0);
        }
    }

    @keyframes slideOut {
        0% {
            transform: translateX(0);
        }
        100%{
            transform: translateX(100);
        }
    }

    .navbar #nav-links-mobile-hide{
        left: -100%;
        animation: slideOut 0.5s ease-in-out;
    }

    .navbar #nav-links-mobile li, .navbar #nav-links-mobile-hide li{
        text-align: center;
        padding: 1rem 0;
        border-bottom: 1px solid var(--color-white);
        margin: 0 3rem;
    }

    .navbar #nav-links-mobile li:last-child, .navbar #nav-links-mobile-hide li:last-child{
        border-bottom: none;
    }


}